import { Component, OnInit, Input, EventEmitter, Output, ViewChild, TemplateRef, ComponentFactoryResolver, Injector, ComponentRef } from '@angular/core';
import { GspComponent } from '@gsp-cmp/common-component';
import { ViewUtils } from '../view-utils/view-utils';
import { ComponentInfo } from '../params/component-info';
import { BsModalRef, BsModalService } from '@farris/ui-modal';
import { RowArgs, CellClickEvent } from '@progress/kendo-angular-grid';
import { ComponentConvertor } from '../convertor/convertor';
import { Operation, StructuredType } from '@ecp-caf/common-structure';

@Component({
    selector: 'common-component-selector',
    templateUrl: './common-component-selector.component.html',
    styleUrls: ['./common-component-selector.component.css']
})
export class CommonComponentSelectorComponent implements OnInit {

    _componentInfo: ComponentInfo;

    @Input() set componentInfo(_componentInfo: ComponentInfo) {
        this._componentInfo = _componentInfo;
        this.metadata=_componentInfo.metadata;

        let convertor = new ComponentConvertor();
        this.metadata = convertor.initFromObject(convertor.convertorToObject(_componentInfo.metadata));
        this.metadata.operations = new Array<Operation>();
    }

    dialog: BsModalRef;

    metadata: GspComponent;

    selectedOpUris: string[] = [];

    isRowSelected = (e: RowArgs) => this.selectedOpUris.indexOf(e.dataItem.uri) >= 0;

    @ViewChild('btnRef') btnRef: TemplateRef<any>;

    @Output() confirmEmitter: EventEmitter<GspComponent> = new EventEmitter();

    @Output() closeEmitter: EventEmitter<any> = new EventEmitter();

    constructor(private utils: ViewUtils, private modalService: BsModalService,private resolver: ComponentFactoryResolver, private injector: Injector) { }

    ngOnInit() {
        this._componentInfo = this._componentInfo || new ComponentInfo();
    }

    confirm() {
        if (!this._componentInfo.metadata || !this._componentInfo.metadata.operations) {
            if (this._componentInfo.metadata.operations.length <= 0) {
                this.utils.showInfo('æ²¡æå¯éçæä»¶æä½', 'info', '', 1500);
                return;
            }
        }
        if (!this.metadata.operations || this.metadata.operations.length <= 0) {
            this.utils.showInfo('è¯·éæ©æä»¶æä½', 'info', '', 1500);
            return;
        }
        if (this.dialog) {
            this.dialog.close();
        }
        this.confirmEmitter.emit(this.metadata);
    }

    close() {
        if (this.dialog) {
            this.dialog.close();
        }
        this.closeEmitter.emit();
    }


    cellClickHandler($event: CellClickEvent) {
        this.selectedOpUris = [$event.dataItem.uri];
        this.metadata.operations = [$event.dataItem];
    }
}
