import { Injectable, Injector, EventEmitter, ComponentFactoryResolver } from '@angular/core';
import { CommonComponentSelectorComponent } from './ccmp-selector/common-component-selector.component';
import { ViewUtils } from './view-utils/view-utils';
import { BsModalService } from '@farris/ui-modal';
import { Options } from './params/options';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { SelectorResult } from './params/selector-result';
import { ComponentInfo } from './params/component-info';

@Injectable({
    providedIn: 'root'
})
export class CommonComponentUiRTService {

    constructor(private resolver: ComponentFactoryResolver, private injector: Injector,
        private util: ViewUtils, private modalService: BsModalService, private http: HttpClient) { }

    /**
     * éç¨æä»¶æä½éæ©å¨
     * @param metadataId éç¨æä»¶åæ°æ®
     * @param sessionId åè½sessionId
     * @param options UIå¯ééç½®åæ°
     */
    public openSelector(metadataId: string, sessionId: string, options?: Options): Observable<SelectorResult> {
        return this.getComponentInfo(metadataId, sessionId).pipe(map(
            (data: ComponentInfo) => {
                if (data.obselete) {
                    this.util.showObseleteDialog(data);
                    return { confirmEvent: new EventEmitter<any>(), closeEvent: new EventEmitter<any>() };
                }
                else {
                    return this.create(data, options);
                }
            })
        )
    }

    /**
     * è·åæä»¶ä¿¡æ¯
     * @param metadataId æä»¶åæ°æ®ID
     * @param sessionId åè½sessionid
     */
    public getComponentInfo(metadataId: string, sessionId: string): Observable<any> {
        let headers = new HttpHeaders();
        headers.set("sessionId", sessionId);
        let url = `/api/runtime/sys/v1.0/common-component/component-info/${metadataId}`;
        return this.http.get(url, { headers: headers }).pipe(map(data => {
            if (data) {
                let cmpInfo = new ComponentInfo();
                cmpInfo.init(data);
                return cmpInfo;
            }
        }));
    }

    private create(componentInfo: ComponentInfo, options: Options): SelectorResult {
        let fac = this.resolver.resolveComponentFactory(CommonComponentSelectorComponent);
        let cmpRef = fac.create(this.injector);
        cmpRef.instance.componentInfo = componentInfo;
        let modalConfig = this.util.getModalConfig(cmpRef.instance.btnRef, options);
        let dialog = this.modalService.show(cmpRef, modalConfig);
        cmpRef.instance.dialog = dialog;
        return { confirmEvent: cmpRef.instance.confirmEmitter, closeEvent: cmpRef.instance.closeEmitter };
    }
}
