    public showInfo(message: string, infoType: string, title: string, timeout: number) {
        this.notify.config.position = 'top-center';
        if (infoType == 'success') {
            this.notify.success(<NotifyOptions>{ title: title, msg: message, timeout: timeout });
        }
        else if (infoType == 'info') {
            this.notify.info(<NotifyOptions>{ title: title, msg: message, timeout: timeout });
        }
        else if (infoType == 'warning') {
            this.notify.warning(<NotifyOptions>{ title: title, msg: message, timeout: timeout });
        }
        else if (infoType == 'error') {
            this.notify.error(<NotifyOptions>{ title: title, msg: message, timeout: timeout });
        }
    }
