import { GspComponent } from '@gsp-cmp/common-component';
import { ComponentConvertor } from '../convertor/convertor';

export class ComponentInfo{
    obselete:boolean;
    id:string;
    code:string;
    name:string;
    nameSpace:string;
    fileName:string;
    type:string;
    bizobjectID:string;
    language:string;
    metadata:GspComponent;


    init(obj:Object){
        this.id=obj['id'];
        this.obselete=obj['obselete'];
        this.code=obj['code'];
        this.name=obj['name'];
        this.nameSpace=obj['nameSpace'];
        this.fileName=obj['fileName'];
        this.type=obj['type'];
        this.bizobjectID=obj['bizobjectID'];
        this.language=obj['language'];
        if(obj['metadata']){
            let convertor = new ComponentConvertor();
            this.metadata = convertor.initFromObject(obj['metadata']);
        }
    }
}